export default {
  name: "rechargeList",
  data() {
      return {
          userList: [],
          autoShow: false,
          changeShow:false,
          is_auto:false,
          checkitem: {},
          integral:'',
      }
  },
  components: {
  },
  onLoad(options) { 
      this.getUserList()
  },
  methods: {
      async cancelAuto(item){
          uni.showLoading({mask: true, title: '加载中...',})
          try{
              let info =  await this.$request({
                  url: this.$api.myrecharge.transfer,
                  method:'post',
                  data: {
                      type:3,
                      uid:this.checkitem.uid, 
                  }
              })
              let { code, data, msg } = info;
              if (code == 1) {
                  this.resetList()
              } else {}
              uni.showToast({title: msg, icon: 'none'});
          }catch(e){
              uni.showToast({title: e, icon: 'none'});
          }
          // uni.hideLoading();;
      },
      openAuto(item){
          this.checkitem = item
          this.autoShow = true
      },
      rechange(item){
          this.checkitem = item
          this.changeShow = true
      },
      async saveauto(){
          let type = 2 // 1自动转赠 2手动转赠 3关闭自动转赠
          if(!this.is_auto){
              return uni.showToast({title: '请选勾选积分转赠规则', icon: 'none'});
          }

          if(this.checkitem.transfer_open == 1){
              type = 1
          }else{
              if(this.checkitem.transfer_close == 1){
                  type = 3
              }else{
                  type = 0
              }
          }
          uni.showLoading({mask: true, title: '加载中...',})
          try{
              let info =  await this.$request({
                  url: this.$api.myrecharge.transfer,
                  method:'post',
                  data: {
                      type:type,
                      uid:this.checkitem.uid, 
                  }
              })
              let { code, data, msg } = info;
              if (code == 1) {
                  this.autoShow = false
                  this.resetList()
              }
              uni.showToast({title: msg, icon: 'none'});
          }catch(e){
              uni.showToast({title: e, icon: 'none'});
          }
          uni.hideLoading();;
          this.autoShow = false
      },
      async sellSave(){
          console.log(this.checkitem, this.integral);
          uni.showLoading({mask: true, title: '加载中...',})
          try{
              let res = await this.$request({
                  url: this.$api.myrecharge.transfer,
                  method:'post',
                  data: {
                      type:2,
                      uid:this.checkitem.uid, 
                      integral:this.integral
                  }
              })
              let { code, data, msg } = res;
              if (code == 1) {
                  this.integral = ''
                  this.resetList()
                  this.changeShow = false
              }
              uni.showToast({title: msg, icon: 'none'});
          }catch(e){
              uni.showToast({title: e, icon: 'none'});
          }
          // uni.hideLoading();;
      },
      resetList(){
          this.page = 1
          this.userList.length = 0
          this.getUserList()
      },
      async getUserList (){
          uni.showLoading({mask: true, title: '加载中...',})
          try{
              let info =  await this.$request({
                  url: this.$api.myrecharge.together,
                  data: {}
              })
              let { code, data, msg } = info;
              if (data && data.length) {
                  this.userList = data
              }
          }catch(e){}
          uni.hideLoading();;
      }
  }
}
